import { home } from '@api';
const { selectArticlesByPage } = home;

export default {
  data() {
    return {
      tabBar: false,
      godNewsArr: [
        {
          name: '全部',
          select: true
        },
        {
          name: '开发语言',
          select: false
        },
        {
          name: 'JAVA开发',
          select: false
        },
        {
          name: '前端开发',
          select: false
        },
        {
          name: '大数据',
          select: false
        },
        {
          name: '软件测试',
          select: false
        },
        {
          name: '开发工具',
          select: false
        },
        {
          name: '数据库',
          select: false
        }
      ],
      godNewsList: [],
      interViewList: []
    };
  },
  mounted() {
    
  },
  async onLoad() {
    const { list } = await selectArticlesByPage();
    // console.info(list)
    // 处理文章列表文字详情过长
    list.forEach(item => (item.summary = item.summary.slice(0, 80) + '...'));
    this.godNewsList = list || [];
    // 面试经验
    this.interViewList = JSON.parse(JSON.stringify(this.godNewsList));
    this.interViewList.forEach(item => (item.summary = item.summary.slice(0, 40) + '...'));
  },
  methods: {
    // 技术类型切换
    movableTab(index) {
      this.godNewsArr.forEach(el => (el.select = false));
      this.godNewsArr[index].select = true;
    }
  }
};
